import { Box, Container, Grid } from "@material-ui/core";
import React from "react";
import { myCertificates } from "../data/certificates";
import { colors } from "../theme/colors.theme";
import Dialog from "./Dialog";

const Certificates = () => {
  const [open, setOpen] = React.useState(false);

  const [selected, setSelected] = React.useState({});

  const { black } = colors;

  const handleOpen = (value) => {
    setOpen(true);
    setSelected(value);
  };

  const handleClose = () => {
    setOpen(false);
    setSelected({});
  };

  return (
    <>
      {open && <Dialog handleClose={handleClose} selected={selected} />}
      <Box component="section" bgcolor={black} p={2}>
        <Box height={50} />
        <Container>
          <h1 className="certificate-section-title">
            <span>
              <i className="fa fa-certificate"></i>CERTIFICATES
            </span>
          </h1>
          <Grid container spacing={1}>
            {myCertificates.map((certificate, index) => (
              <Grid
                item
                xs={12}
                md={4}
                lg={4}
                key={certificate.name}
                onClick={() => handleOpen({ ...certificate, index })}
              >
                <div className="certificate-container animatable bounceIn">
                  <div className="certificate-container-image">
                    <img src={certificate.image} alt="Certificado" />
                  </div>
                  <div className="certificate-container-text">
                    {certificate.name}
                  </div>
                </div>
              </Grid>
            ))}
          </Grid>
        </Container>
        <Box height={50} />
      </Box>
    </>
  );
};

export default Certificates;
