import React from "react";
import { Box, Container } from "@material-ui/core";
import Timeline from "@material-ui/lab/Timeline";
import TimelineItem from "@material-ui/lab/TimelineItem";
import TimelineSeparator from "@material-ui/lab/TimelineSeparator";
import TimelineConnector from "@material-ui/lab/TimelineConnector";
import TimelineContent from "@material-ui/lab/TimelineContent";
import TimelineOppositeContent from "@material-ui/lab/TimelineOppositeContent";
import TimelineDot from "@material-ui/lab/TimelineDot";
import HomeIcon from "@material-ui/icons/Home";
import VideogameAssetIcon from "@material-ui/icons/VideogameAsset";
import EmojiEventsIcon from "@material-ui/icons/EmojiEvents";
import OpacityIcon from "@material-ui/icons/Opacity";
import Typography from "@material-ui/core/Typography";
import { colors } from "../theme/colors.theme";

const Experience = () => {
  const { black, white, blue3C, redDark } = colors;
  const experiences = [
    {
      time: "2018 - Present",
      title: "Co-Founder & CTO @ Torneos.GG",
      description: `In charge of the technology area. I am also the frontend manager.`,
      image: "./assets/images/tgg.png",
      icon: <EmojiEventsIcon />,
      color: "secondary",
    },
    {
      time: "2019 - 2020",
      title: "ASADA-SOP (University Project)",
      description: `Using digital transformation, a web platform was created to manage the different processes of an ASADA (Associations of the Communal Aqueduct and Sewer Systems).`,
      image: "./assets/images/asadasop.png",
      icon: <OpacityIcon />,
      color: "primary",
      variant: "outlined",
    },
    {
      time: "2020",
      title: "Collaboration as Full Stack in PANGEA LIVING",
      description: `I made some systems related to the reservation of rooms and also in the administrative system (control of rooms, reservations, etc). I worked as a Full Stack.`,
      image: "./assets/images/pangea.png",
      icon: <HomeIcon />,
      color: "secondary",
      variant: "outlined",
    },
    {
      time: "2020 - Present",
      title: "Frontend Developer @ Gamanza",
      description: `Realization of different UI components and views for information management in a CRM system.`,
      image: "./assets/images/gamanza.png",
      icon: <VideogameAssetIcon />,
      color: "primary",
    },
  ];
  return (
    <Box component="section" p={3} bgcolor={black} color={white}>
      <Container>
        <h1 className="section-skills-title">Experience</h1>
        <Timeline align="alternate">
          {experiences.map((exp) => (
            <TimelineItem key={exp.title}>
              <TimelineOppositeContent>
                <Box color={white}>
                  <Typography variant="body2">{exp.time}</Typography>
                </Box>
              </TimelineOppositeContent>
              <TimelineSeparator>
                <TimelineDot color={exp.color} variant={exp.variant}>
                  {exp.icon}
                </TimelineDot>
                <TimelineConnector />
              </TimelineSeparator>
              <TimelineContent>
                <Box bgcolor={exp.color === "primary" ? blue3C : redDark} color={white} p={1} borderRadius={10}>
                  <Typography
                    variant="h6"
                    component="h1"
                    style={{ fontWeight: 700 }}
                  >
                    {exp.title}
                  </Typography>
                  <Typography>{exp.description}</Typography>
                </Box>
                <Box textAlign="right" mt={1} mb={1}>
                  <img src={exp.image} style={{ width: 150 }} alt={exp.title} />
                </Box>
              </TimelineContent>
            </TimelineItem>
          ))}
        </Timeline>
      </Container>
    </Box>
  );
};

export default Experience;
