import React from "react";
import { skills } from "../data/skills";

const Skills = () => {
  const { devSkills, designerSkills, softSkills } = skills;

  return (
    <section className="skills-section">
      <div className="container">
        <h1 className="section-skills-title">Skills</h1>
        <div className="row">
          <div className="col-sm-12 col-md-4">
            <p className="skills-title animatable moveUp">
              <i className="fa fa-cog"></i>Developer
            </p>
            {devSkills.map((dev) => (
              <div
                key={dev.techologie}
                className="skill-container animatable moveUp"
              >
                <div className="skill-container-title skill-gold">
                  {dev.techologie}
                </div>
                <div className="skill-container-tech skill-dev">
                  {dev.skill}
                </div>
              </div>
            ))}
          </div>

          <div className="col-sm-12 col-md-4">
            <p className="skills-title animatable moveUp">
              <i className="fa fa-paint-brush"></i>Designer
            </p>
            {designerSkills.map((designer) => (
              <div
                key={designer.techologie}
                className="skill-container animatable moveUp"
              >
                <div className="skill-container-title skill-purple">
                  {designer.techologie}
                </div>
                <div className="skill-container-tech skill-designer">
                  {designer.skill}
                </div>
              </div>
            ))}
          </div>

          <div className="col-sm-12 col-md-4">
            <p className="skills-title animatable moveUp">
              <i className="fa fa-bolt"></i>Soft
            </p>

            <div className="skill-container animatable moveUp">
              <div className="skill-container-title skill-red">ENGLISH</div>
              <div className="skill-container-tech skill-other">INTERMEDIATE</div>
            </div>

            {softSkills.map((soft) => (
              <div
                key={soft.skill}
                className="skill-container animatable moveUp"
              >
                <div className="skill-container-title skill-red">
                  {soft.skill}
                </div>
                <div className="skill-container-tech skill-other-nothing"></div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </section>
  );
};

export default Skills;
