import React from "react";
import {
  makeStyles,
  Grid,
  Card,
  CardActions,
  CardContent,
  CardMedia,
  Button,
  Typography,
  Box,
} from "@material-ui/core";
import { colors } from "../theme/colors.theme";

const useStyles = makeStyles({
  root: {
    backgroundColor: colors.blueDark,
    color: colors.white,
  },
});

const Projects = () => {
  const classes = useStyles();

  const myProjects = [
    {
      name: "ASADA-SOP",
      description: "Process management and streamlining system for an ASADA",
      image: "./assets/images/proyectos/asada.jpg",
      technologies: [
        {
          name: "Laravel",
          badge: "primary",
        },
        {
          name: "MySQL",
          badge: "success",
        },
        {
          name: "Bootstrap",
          badge: "dark",
        },
      ],
    },
    {
      name: "TORNEOS.GG",
      description: "Tournaments platform for eSports video games",
      image: "./assets/images/proyectos/tgg.jpg",
      url: "https://torneos.gg/",
      technologies: [
        {
          name: "ReactJS",
          badge: "primary",
        },
        {
          name: "MongoDB",
          badge: "success",
        },
        {
          name: "Material-UI",
          badge: "dark",
        },
      ],
    },
    {
      name: "PANGEA LIVING",
      description: "Platform to reserve rooms in residential",
      image: "./assets/images/proyectos/pangea.jpg",
      url: "https://pangealivingmde.com/",
      technologies: [
        {
          name: "ReactJS",
          badge: "primary",
        },
        {
          name: "MongoDB",
          badge: "success",
        },
        {
          name: "Ant Design",
          badge: "dark",
        },
      ],
    },
  ];

  const openNewTab = (url) => {
    window.open(url, "_blank", "noopener");
  };

  return (
    <section className="projects">
      <div className="container">
        <h1 className="section-skills-title">Projects</h1>
        <Grid container spacing={1}>
          {myProjects.map((project) => (
            <Grid
              key={project.name}
              className="animatable fadeInDown"
              item
              xs={12}
              sm={12}
              md={4}
              lg={4}
            >
              <Box height="100%">
                <Card className={classes.root}>
                    <CardMedia
                      component="img"
                      alt={project.name}
                      height="200"
                      image={project.image}
                      title={project.name}
                    />
                    <CardContent>
                      <Typography gutterBottom variant="h5" component="h2" style={{fontWeight:700}}>
                        {project.name}
                      </Typography>
                      <Typography
                        variant="body2"
                        component="p"
                      >
                        {project.description}
                      </Typography>
                    </CardContent>
                  <CardActions>
                    <Button
                      onClick={()=>openNewTab(project.url)}
                      disabled={!project.url}
                      size="small"
                      color="primary"
                      variant="contained"
                      style={{
                        backgroundColor: project.url
                          ? colors.blue
                          : colors.blueDark,
                        fontWeight: 700,
                        color: colors.white,
                      }}
                    >
                      See
                    </Button>

                    <p className="card-text">
                      {project.technologies.map((tech) => (
                        <span
                          key={tech.name}
                          style={{ marginRight: 4 }}
                          className={"badge rounded-pill bg-" + tech.badge}
                        >
                          {tech.name}
                        </span>
                      ))}
                    </p>
                  </CardActions>
                </Card>
              </Box>
            </Grid>
          ))}
        </Grid>
        <Box height="30px" />
      </div>
    </section>
  );
};

export default Projects;
